___CSS_LOADER_EXPORT___.push([module.id, `.task-form-container {
    display: none;
    flex-direction: column;
    padding: 10px;
    border-radius: 0.6rem;
    border: 1px solid #9496a0;
}

#task-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.field-container {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    width: 100%;
}

.task-field{ 
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    border: none;
    padding: 4px;
}

.task-field:focus-visible {
    outline: none;
}

.task-btn-container {
    display: flex;  
    gap: 1rem;
}
.task-btn-wrapper {
    display: flex;
    padding: 0.3em .5em;
    border: 1px solid #DBDFEA;
    border-radius: 0.3em;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.task-btn-wrapper:hover {
    background-color: #f2f2f3;
}
.btn-icon {
    width: 1rem;
}
.task-btn {
    cursor: pointer;
    background: none;
    border: none;
}

.control-btn-container {
    position: relative;
    display: flex;
    gap: 1rem;
    justify-content: flex-end;
    border-top: 1px solid #9496a0;
    padding: 0.7rem 0 0 0;
}

.control-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.7em 0.9em;
    border-radius: 0.3rem;
    border: none;
    color: #3f3f3f;
    font-weight: 900;
}

.control-btn:hover {
 background-color: #DBDFEA;   
}
#form-add-task-btn {
    background-color: #ACB1D6;
    color: #fff;
}
#form-add-task-btn:hover {
    background-color: #8294C4;
}`, "",{"version":3,"sources":["webpack://./src/css/task-form.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,qBAAqB;IACrB,yBAAyB;AAC7B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,WAAW;IACX,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,SAAS;AACb;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,oBAAoB;IACpB,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,yBAAyB;AAC7B;AACA;IACI,WAAW;AACf;AACA;IACI,eAAe;IACf,gBAAgB;IAChB,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,SAAS;IACT,yBAAyB;IACzB,6BAA6B;IAC7B,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,oBAAoB;IACpB,qBAAqB;IACrB,YAAY;IACZ,cAAc;IACd,gBAAgB;AACpB;;AAEA;CACC,yBAAyB;AAC1B;AACA;IACI,yBAAyB;IACzB,WAAW;AACf;AACA;IACI,yBAAyB;AAC7B","sourcesContent":[".task-form-container {\n    display: none;\n    flex-direction: column;\n    padding: 10px;\n    border-radius: 0.6rem;\n    border: 1px solid #9496a0;\n}\n\n#task-form {\n    display: flex;\n    flex-direction: column;\n    gap: 1rem;\n}\n\n.field-container {\n    display: flex;\n    flex-direction: column;\n    gap: 0.2rem;\n    width: 100%;\n}\n\n.task-field{ \n    display: flex;\n    align-items: center;\n    justify-content: flex-start;\n    width: 100%;\n    border: none;\n    padding: 4px;\n}\n\n.task-field:focus-visible {\n    outline: none;\n}\n\n.task-btn-container {\n    display: flex;  \n    gap: 1rem;\n}\n.task-btn-wrapper {\n    display: flex;\n    padding: 0.3em .5em;\n    border: 1px solid #DBDFEA;\n    border-radius: 0.3em;\n    justify-content: center;\n    align-items: center;\n    cursor: pointer;\n}\n\n.task-btn-wrapper:hover {\n    background-color: #f2f2f3;\n}\n.btn-icon {\n    width: 1rem;\n}\n.task-btn {\n    cursor: pointer;\n    background: none;\n    border: none;\n}\n\n.control-btn-container {\n    position: relative;\n    display: flex;\n    gap: 1rem;\n    justify-content: flex-end;\n    bo